.navbar {
    position: absolute;
    top: 0;
    left: 0;
    width: 50px;
    height: 50px;
    transition: width 0.5s, height 0.5s;
    transition-delay: 0.75s, 0s;
    z-index: 150;
    background: transparent;
    overflow: hidden;
}
.navbar.active {
    transition: width 0.5s, height 0.5s;
    transition-delay: 0s, 0.75s;
    width: 50%;
    height: 100%;
    background: rgba(255,255,255,0.3);
    overflow: hidden;
    box-shadow: 20px 20px 50px rgba(0,0,0,0.5);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.navbar .toggle {
    position: relative;
    top: 0;
    left:0;
    width: 100%;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: left;
            justify-content: left;
    -webkit-align-items: center;
            align-items: center;
    /* background: transparent; */
    transition: 0.5s;
    cursor: pointer;
    font-size: 2em;
    color: #eee;
    transition: 0.5s;
}
.navbar .toggle.active {
    /* background: #eee; */
    color: #eee;
}


.navbar ul {
    list-style: none;
}
.navbar ul li {
    padding: .5rem;
    transition: 0.5s;
    margin-top: 20px;
    
}
.navbar ul li:hover {
    text-decoration: underline;
    cursor: pointer;
}
.navbar ul li a {
    text-decoration: none;
    color: #fff;
}
.navbar ul li .icons {
    margin-right: 20px;
    margin-left: 20px;
}
