.todo-container {
    max-height: 65vh;
    width: 90%;
    z-index: 10;
    overflow: auto;
}
.todo-list {
    list-style: none;
}
.todo {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.todo-item {
    display: grid;
    grid-template-columns: 1fr 35px;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    padding: 0 20px;
    list-style: none;
    width: 100%;
    padding: 0.5rem;
    background: #3a4750;
    border-radius: 4px;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    /* box-shadow: 0 8px 24px rgba(56,56,56, 0.15); */
    /* background: #3a4750; */
    margin: 10px 0;
}
.todo-item.completed {
    text-decoration: line-through;
    color: rgba(255,255,255,0.50);
}
.todo-item .content {
    display: grid;
    grid-template-columns: 50px 1fr;
    -webkit-align-items: center;
            align-items: center;
}
.todo-item .content:nth-child(1) {
    font-size: 15pt;
}
.todo-item .trash {
    cursor: pointer;
    font-size: 17pt;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    /* width: 85%; */
}
.todo-item .trash:hover {
    color: #f6c90e;
}
.form {
    position: absolute;
    bottom: 5%;
    right: 5%;
    width: 50px;
    height: 50px;
    background: #fff;
    /* background: #303841; */
    transition: width 0.5s, height 0.5s;
    transition-delay: 0.75s, 0s;
    z-index: 100;
    border-radius: 4px;
    overflow: hidden;
    
}
.form.active {
    width: 85%;
    /* height: 350px; */
    height: 130px;
    transition: width 0.5s, height 0.5s;
    transition-delay: 0s, 0.75s;
}
.form .toggle {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    /* background: #303841; */
    background: #fff;
    transition: 0.5s;
    cursor: pointer;
}
.form .toggle.active {
    background: #3a4750;
}
.form .toggle::before {
    content: '+';
    font-size: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 50px;
    height: 50px;
    /* color: #fff; */
    color: #303841;
    font-weight: 400;
    transition: 0.5s;
}
.form .toggle.active::before {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
    color: #fff;
}
.form .input {
    position:absolute;
    left: 0;
    width: 100%;
    height: 80px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    padding: 19px;
    
}

.input input, .input button {
    padding: 0.5rem;
    font-size: 12pt;
    background: #fff;
    outline: none;
    border: none;
}
.input input {
    width: 85%;
    border: 1px solid #6699CC;
}
.input button {
    color: #f6c90e;
    background: #eee;
    cursor: pointer;
    transition: 0.5s;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
}
.input button:hover {
    background: #f6c90e;
    color: #eee;
}



